var utils = require("../../util.js");

class GrammarEnabledTextEditor {
  constructor(domNode, {
    body = '',
    changeListeners = [],
    settledChangeListeners = []
  }){
    utils.abstract(new.target, GrammarEnabledTextEditor);

    this.domNode = domNode;
    this.editor = CodeMirror(this.domNode);
    this.editor.setOption("extraKeys", {
      Tab: function(cm) {
        var spaces = Array(cm.getOption("indentUnit") + 1).join(" ");
        cm.replaceSelection(spaces);
      }
    });

    this.settledChangeListeners = changeListeners;
    this.changeListeners = settledChangeListeners;

    this.settleTimeout = null;
    this.marks = [];
    this.editor.on('change', ()=>{
      let [valid, nodes] = this._parseText(this.editor.getValue());
      this._syntaxHighlight(nodes);
      this.onChange(valid);

      if(this.settleTimeout){clearTimeout(this.settleTimeout);}
      this.settleTimeout = setTimeout(this.onSettledChange.bind(this, valid), 500);
    });
  }

  onSettledChange(valid){
    this.settledChangeListeners.forEach(listener=> listener(valid));
    this.previousTimeout = null;
  }

  onChange(valid){
    this.changeListeners.forEach(listener=> listener(valid));
  }
}


var toExport = {
  GrammarEnabledTextEditor
};

if(typeof module !== "undefined" && typeof module.exports !== "undefined"){
  module.exports = toExport;
} else {
  Object.assign(window, toExport);
}



