var GrammarEnabledTextEditor = require("./grammarEnabledTextEditor.js").GrammarEnabledTextEditor;

class OhmGrammarTextEditor extends GrammarEnabledTextEditor{
  constructor(domNode, {
    body = '',
    grammar,
    changeListeners = [],
    settledChangeListeners = []
  }){
    super(domNode, {body, changeListeners, settledChangeListeners});
    this.grammar = grammar;

    this.semantics = grammar.semantics().addOperation('makeArray(nodes)', {
      _nonterminal(children){
        this.args.nodes.push(this._node);

        let ruleName = this.ctorName;
        if (this.isSyntactic() ||
            ruleName === 'tokens' || ruleName === 'keyword' || ruleName === 'space' || ruleName === 'spaces') {
          this.children.forEach(child=> child.makeArray(this.args.nodes));
        }

        return this.args.nodes;
      },
      _terminal(){
        this.args.nodes.push(this._node);
        return this.args.ndoes;
      }
    });

    let keyword = grammar.ruleBodies.keyword;
    if(keyword){
      this.keywordClasses = new Set(keyword.terms.map((term)=> term.ruleName));
    }

    //trigger highlight
    this.editor.setValue(body);
  }

  _parseText(exampleText){
    let match = this.grammar.match(exampleText);
    let valid, nodes;
    if(match.succeeded()){
      valid = true;
      nodes = this.semantics(match).makeArray([]);
      this.semantics(match.getDiscardedSpaces()).makeArray(nodes);
    } else {
      valid = false;
      match = this.grammar.match(exampleText, 'tokens');
      nodes = this.semantics(match).makeArray([]);
    }

    return [valid, nodes];
  }

  _syntaxHighlight(nodes){
    nodes.forEach((node)=>{
      var doc = this.editor.doc;
      this.marks.push(doc.markText(
          doc.posFromIndex(node.interval.startIdx),
          doc.posFromIndex(node.interval.endIdx),
          {className: node.ctorName}
      ));
      if(this.keywordClasses
        && this.keywordClasses.has(node.ctorName)){
        this.marks.push(doc.markText(
            doc.posFromIndex(node.interval.startIdx),
            doc.posFromIndex(node.interval.endIdx),
            {className: 'keyword'}
        ));
      }
    });
  }
}


var toExport = {
  OhmGrammarTextEditor
};

if(typeof module !== "undefined" && typeof module.exports !== "undefined"){
  module.exports = toExport;
} else {
  Object.assign(window, toExport);
}



